@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e2e2e2;
}


.navbar-brand img{
    height:2.8rem;
    width: 3rem;
    margin-top: .2rem;
    padding-right: 10px;
} 
.navbar-brand{
    margin-left: none;
}

.cart-btn{
    margin-right: none;
    border-radius: 12px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: #F8F9FA;
    border: rgb(214, 211, 211) 1px solid;
}

.cartLogo{
    padding-right: 9px;
}

.badge{
    padding: 5px 10px;
    margin-left: 12px;
    color: black;
    border: rgb(239,191,26) 1px solid;
}


@media only screen and (min-width: 800px){
    .navbar-brand{
        margin-left: 20rem;
    }
    .cart-btn{
        margin-right: 20rem;
    }
}

@media screen and (min-width:500px) and (max-width:1000px){
    .navbar-brand{
        margin-left: 5rem;
    }
    .cart-btn{
        margin-right: 5rem;
    }
 }

@media only screen and (max-width: 332px){
    .navbar-brand{
        font-size: 1.1rem;
    }
    .cart-btn{
        font-size: 1.1rem;
    }
}

.btn-cancel{
    color: black;
    background-color: white;
    border: 1px solid #ffdf7d;
    padding: 9px 15px 9px 15px;
}
.btn-cancel:hover{
    color: black;
    background-color: #ffdf7d;
    border: 1px solid #ffdf7d;
}

.btn-order{
    color: black;
    background-color: #ffdf7d;
    border: 1px solid #ffdf7d;
   padding: 9px 15px 9px 15px;
}

.btn-order:hover{
    color: black;
    background-color: #ffdf7d;
    border: 1px solid #ffdf7d;
}

.order-form div{
    display: -webkit-flex;
    display: flex;  
    padding: 5px;
    padding-left: 50px;
    padding-right: 50px;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}
.order-form label{
    padding-bottom: 5px;
}
.order-form button{
    margin-left: 50px;
    /* margin: auto; */
    margin-bottom: 10px;
    margin-top: 10px;
}
.imageItem{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 50%;
}

.caption{
    color: black;
    display: -webkit-flex;
    display: flex;
    float: left; 
    top: 0rem;
    display: flex !important;
    -webkit-flex-direction: row !important;
            flex-direction: row !important;
    -webkit-align-items: center;
            align-items: center;
}

.iceCream{
    color: rgb(255, 255, 255);
    text-align: left;
}

.headCap{
    margin-bottom: 20px;
    padding:0;
    border-bottom: 2px solid #c49b34;
}

.card{
    margin-bottom: 4rem;
    width: 100%;
}


.inputNum{
    width:50px;
    -webkit-align-items: right;
            align-items: right;
    margin-right: 6px;
    border: none;
}

.Addbutton{
    display: -webkit-flex;
    display: flex;
    float: right;
    border: 1px solid rgb(239,191,26);
    border-radius: 10px;
    background-color: white;
}

.main{
   text-align: center;
   padding-top: 3rem;
}

.mainLink{
    text-align: center;
    padding-top: 1rem;
    margin-bottom: 3rem;
 }
 .mainLink a{
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 15px;
    padding-bottom: 5px;
    text-decoration: none;
    color: #606060;
 }
 .mainLink a:hover{
    color: #bd8c11;
    border-bottom: 1px solid #bd8c11;
 }
